/* Base Styles (default theme: white) */
:root {
    --bg-color: #ffffff;
    --text-color: #333333;
    --link-color: #0073e6;
    --button-bg: #ffffff;
    --button-border: #333333;
    --button-text: #333333;
}

/* Black Theme */
body.black-theme {
    --bg-color: #000000;
    --text-color: #ffffff;
    --link-color: #1e90ff;
    --button-bg: #333333;
    --button-border: #ffffff;
    --button-text: #ffffff;
}

/* Light Red Theme */
body.red-theme {
    --bg-color: #ffecec;
    --text-color: #4a0000;
    --link-color: #b30000;
    --button-bg: #ffcccc;
    --button-border: #b30000;
    --button-text: #4a0000;
}

/* Apply the theme variables */
body {
    background-color: var(--bg-color);
    color: var(--text-color);
    font-family: 'Arial', sans-serif;
}

a {
    color: var(--link-color);
    text-decoration: none;
}

a:hover {
    text-decoration: underline;
}

button, .project a {
    background-color: var(--button-bg);
    color: var(--button-text);
    border: 1px solid var(--button-border);
    padding: 5px 10px;
    border-radius: 3px;
    transition: background-color 0.3s ease, color 0.3s ease;
}

button:hover, .project a:hover {
    background-color: var(--button-border);
    color: var(--button-bg);
}

/* About Page Specific Styles */
.about-content {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    gap: 0px;
}

.profile-pic {
    max-width: 300px;
    width: 100%;
    height: auto;
    margin-left: 200px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    object-fit: cover;
}

.info {
    flex: 1;
    margin-left: -75px;
}

.description {
    margin-top: 20px;
    text-align: justify;
    line-height: 1.8;
    font-size: 16px;
    max-width: 800px;
    margin-left: auto;
    margin-right: auto;
}

.links {
    display: flex;
    flex-wrap: wrap;
    gap: 24px;
    justify-content: center;   /* Center buttons horizontally */
    margin-top: 20px;
}

.about-link {
    background: #e9f1ff;
    color: #3566b7;
    border: 1px solid #b4d0fb;
    border-radius: 6px;
    padding: 5px 16px;
    font-size: 1em;
    font-weight: 500;
    text-decoration: none;
    transition: background 0.16s, color 0.16s, border 0.16s;
    display: inline-block;
    margin: 0;
    /* Optional: add shadow for pop */
    box-shadow: 0 1px 4px rgba(53, 102, 183, 0.06);
}

.about-link:hover {
    background: #3566b7;
    color: #fff;
    border-color: #3566b7;
}