/* Default Theme (White) */
:root {
    --blog-bg-color: #ffffff;
    --blog-text-color: #333333;
    --blog-link-color: #0073e6;
    --blog-link-bg: #ffffff;
    --blog-link-border: #333333;
    --blog-link-hover-bg: #333333;
    --blog-link-hover-text: #ffffff;
}

/* Black Theme */
body.black-theme {
    --blog-bg-color: #2c2c2c;
    --blog-text-color: #ffffff;
    --blog-link-color: #1e90ff;
    --blog-link-bg: #333333;
    --blog-link-border: #ffffff;
    --blog-link-hover-bg: #ffffff;
    --blog-link-hover-text: #333333;
}

/* Light Red Theme */
body.red-theme {
    --blog-bg-color: #ffecec;
    --blog-text-color: #4a0000;
    --blog-link-color: #b30000;
    --blog-link-bg: #ffcccc;
    --blog-link-border: #b30000;
    --blog-link-hover-bg: #b30000;
    --blog-link-hover-text: #ffffff;
}

/* Blog Post Styling */
.blog-post {
    background-color: var(--blog-bg-color);
    color: var(--blog-text-color);
    padding: 20px;
    margin: 20px 0;
    border-radius: 5px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.blog-post h2 {
    margin-bottom: 10px;
    color: var(--blog-text-color);
}

.blog-post a {
    display: inline-block;
    margin-top: 10px;
    color: var(--blog-link-color);
    text-decoration: none;
    border: 1px solid var(--blog-link-border);
    padding: 5px 10px;
    border-radius: 3px;
    transition: background-color 0.3s ease, color 0.3s ease;
    background-color: var(--blog-link-bg);
}

.blog-post a:hover {
    background-color: var(--blog-link-hover-bg);
    color: var(--blog-link-hover-text);
}