.project {
    background-color: #fff;
    padding: 20px;
    margin: 20px 0;
    border-radius: 5px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

body.dark-mode .project {
    background-color: #2c2c2c;
    color: #ffffff;
}

.project h2 {
    margin-bottom: 10px;
}

.project p {
    margin: 10px 0;
}

.project .technologies {
    font-style: italic;
    font-size: 14px;
    color: #555; /* Subtle color for the technology section */
    margin-top: 5px;
}

.project .technologies span {
    color: #333; /* Darker color for the technologies list */
}

.project a {
    display: inline-block;
    margin-top: 10px;
    color: #333;
    text-decoration: none;
    border: 1px solid #333;
    padding: 5px 10px;
    border-radius: 3px;
    transition: background-color 0.3s ease, color 0.3s ease;
}

.project a:hover {
    background-color: #333;
    color: #fff;
}

.project .citation-link {
    display: inline; /* Remove block behavior */
    margin: 0; /* Reset margins to default */
    color: #0073e6; /* Blue color for the citation */
    text-decoration: underline; /* Show as an underlined text link */
    border: none; /* Remove the border applied by .project a */
    padding: 0; /* Remove padding */
    font-style: italic; /* Optional: Make the link italicized */
    font-size: 14px; /* Adjust size for readability */
}

.project .citation-link:hover {
    color: #005bb5; /* Slightly darker blue on hover */
    text-decoration: none; /* Remove underline on hover for effect */
}