/* Default Theme (White) */
:root {
    --navbar-bg: #333333;
    --navbar-text: #ffffff;
    --navbar-hover: #f4f4f4;
    --navbar-toggle-bg: none;
    --navbar-toggle-text: #ffffff;
}

/* Black Theme */
body.black-theme {
    --navbar-bg: #000000;
    --navbar-text: #ffffff;
    --navbar-hover: #1e1e1e;
    --navbar-toggle-bg: none;
    --navbar-toggle-text: #ffffff;
}

/* Light Red Theme */
body.red-theme {
    --navbar-bg: #ffecec;
    --navbar-text: #4a0000;
    --navbar-hover: #ffcccc;
    --navbar-toggle-bg: none;
    --navbar-toggle-text: #4a0000;
}

/* Navbar Styling */
.navbar {
    background-color: var(--navbar-bg);
    color: var(--navbar-text);
    padding: 10px 0;
    position: fixed;
    width: 100%;
    top: 0;
    z-index: 1000;
}

.navbar-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 90%;
    max-width: 1200px;
    margin: auto;
}

.logo a {
    color: var(--navbar-text);
    text-decoration: none;
    font-size: 1.5em;
    font-weight: bold;
}

.nav-links {
    list-style: none;
    display: flex;
}

.nav-links li {
    margin-left: 20px;
}

.nav-links a {
    color: var(--navbar-text);
    cursor: pointer;
    transition: color 0.3s ease;
}

.nav-links a:hover {
    color: var(--navbar-hover);
}

.toggle-button {
    background: var(--navbar-toggle-bg);
    border: none;
    color: var(--navbar-toggle-text);
    cursor: pointer;
    font-size: 1em;
}

.menu-toggle {
    display: none;
    color: var(--navbar-toggle-text);
    font-size: 1.5em;
}

/* Responsive Design */
@media (max-width: 768px) {
    .nav-links {
        flex-direction: column;
        background-color: var(--navbar-bg);
        position: absolute;
        top: 60px;
        right: 0;
        width: 100%;
        display: none;
    }

    .nav-links.active {
        display: flex;
    }

    /* Show menu toggle button */
    .menu-toggle {
        display: block;
    }

    .nav-links li {
        margin: 10px 0;
        text-align: center;
    }
}

@media (min-width: 769px) {
    .menu-toggle {
        display: none;
    }
}