/* Default Theme (White) */
:root {
    --contact-bg: #ffffff;
    --contact-text: #333333;
    --contact-input-bg: #ffffff;
    --contact-input-text: #333333;
    --contact-input-border: #333333;
    --contact-button-bg: #333333;
    --contact-button-text: #ffffff;
    --contact-button-hover-bg: #555555;
    --contact-button-hover-text: #ffffff;
}

/* Black Theme */
body.black-theme {
    --contact-bg: #2c2c2c;
    --contact-text: #ffffff;
    --contact-input-bg: #2c2c2c;
    --contact-input-text: #ffffff;
    --contact-input-border: #ffffff;
    --contact-button-bg: #ffffff;
    --contact-button-text: #121212;
    --contact-button-hover-bg: #f4f4f4;
    --contact-button-hover-text: #121212;
}

/* Light Red Theme */
body.red-theme {
    --contact-bg: #ffecec;
    --contact-text: #4a0000;
    --contact-input-bg: #fff5f5;
    --contact-input-text: #4a0000;
    --contact-input-border: #b30000;
    --contact-button-bg: #ffcccc;
    --contact-button-text: #4a0000;
    --contact-button-hover-bg: #b30000;
    --contact-button-hover-text: #ffffff;
}

/* Contact Form Styling */
.contact-form {
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: var(--contact-bg);
    color: var(--contact-text);
}

.contact-form input, 
.contact-form textarea {
    width: 100%;
    max-width: 500px;
    padding: 10px;
    margin-bottom: 15px;
    background-color: var(--contact-input-bg);
    color: var(--contact-input-text);
    border: 1px solid var(--contact-input-border);
    border-radius: 5px;
}

.contact-form button {
    padding: 10px 20px;
    border: none;
    background-color: var(--contact-button-bg);
    color: var(--contact-button-text);
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease, color 0.3s ease;
}

.contact-form button:hover {
    background-color: var(--contact-button-hover-bg);
    color: var(--contact-button-hover-text);
}