.research-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 1000px;
  margin: 0 auto;
  padding: 48px 24px 32px 24px;
  background: #fff;
  border-radius: 20px;
  box-shadow: 0 2px 32px rgba(0,0,0,0.07);
}

.research-title {
  text-align: center;
  font-size: 2.4rem;
  margin-bottom: 10px;
  font-weight: 700;
  letter-spacing: 1px;
}

.research-subtitle {
  text-align: center;
  color: #444;
  margin-bottom: 32px;
  font-size: 1.1rem;
  line-height: 1.7;
  max-width: 80%;
}

.research-section {
  width: 100%;
  margin-bottom: 32px;
}

.research-section h2 {
  text-align: left;
  font-size: 1.3rem;
  font-weight: 600;
  margin-bottom: 12px;
  color: #222;
  border-left: 3px solid #4287f5;
  padding-left: 10px;
}

.research-list {
  list-style: none;
  padding-left: 0;
  margin: 0;
}

.research-list li {
  margin-bottom: 20px;
  font-size: 1.05rem;
  line-height: 1.7;
  display: flex;
  flex-wrap: wrap;
  gap: 0.7em;
  align-items: center;
}

.research-link {
  margin-left: 10px;
  background: #e9f1ff;
  color: #3566b7;
  border: 1px solid #b4d0fb;
  border-radius: 6px;
  padding: 3px 12px;
  font-size: 0.96em;
  text-decoration: none;
  font-weight: 500;
  transition: background 0.15s, color 0.15s;
}

.research-link:hover {
  background: #3566b7;
  color: #fff;
  border-color: #3566b7;
}

@media (max-width: 700px) {
  .research-content {
    max-width: 98vw;
    padding: 18px 5vw 18px 5vw;
  }
  .research-title {
    font-size: 2rem;
  }
  .research-section h2 {
    font-size: 1.1rem;
  }
}